body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  margin-top: 20px;
}
.modal-footer {
  -webkit-justify-content: center;
          justify-content: center;
}

.modal-body {
  padding: 0px;
}

.modal-body, .modal-footer, .modal-header {
  border: none;
}

.modal .btn {
  border-radius: 25px;
}

.modal-contents {
  padding: 20px 0;
}

.modal-footer-area {
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 25px; 
}

.icon .far {
  font-size: 100px;
}

.icon {
  padding: 20px 0;
}

.danger .modal-header, .danger .modal-body, .danger .modal-footer {
  background: #dc3545;
  color: #fff;
}

.success .modal-header, .success .modal-body, .success .modal-footer {
  background: #28a745;
  color: #fff;
}

.danger .modal-header .close {
  color: #fff;
}

.modal .btn-outline-success, .modal .btn-outline-danger {
  color: #fff;
  border: 2px solid #fff;
}

.modal-content {
  border: none;
}

.modal-content, .modal-body {
  border-radius: 30px;
}
